<template>
  <div class="row no-gutters">
    <template v-if="playlist === undefined && playlistAvailable">
      <b-spinner :label="`Loading playlist titled ${title}`"></b-spinner>
    </template>

    <template v-else-if="!playlistAvailable">
      <p>The playlist is currently not available.</p>
    </template>

    <template v-else>
      <div class="col-12 col-md-11">
        <ul class="playlist list--reset">
          <li
            v-for="(track, trackIndex) in tracks"
            v-bind:key="track.id"
            class="playlist__item"
            :class="{
              'playlist__item--current': songIndex === trackIndex
            }"
          >
            <button
              class="playlist__btn btn--reset"
              @click="playTrack(url, trackIndex, track.id)"
            >
              <div class="playlist__title text-truncate">{{ track.title }}</div>
              <div class="playlist__icon">
                <i v-if="trackIsPlaying(track.id)" class="fas fa-pause"></i>
                <i v-else class="fas fa-play"></i>
              </div>
            </button>
          </li>
        </ul>
      </div>
    </template>
  </div>
</template>

<script>
import { mapMutations, mapState, mapActions } from "vuex";
import scPlayer from "@/utilities/soundcloud";

export default {
  name: "Playlist",
  props: {
    maximum: {
      type: Number,
      default: 5
    },
    preload: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      required: true
    },
    url: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      checkDelay: 5000
    };
  },
  computed: {
    ...mapState([
      "api_available",
      "current_song",
      "player",
      "playlists",
      "playlist_current"
    ]),

    playlist() {
      return this.playlists[this.playlistUri];
    },
    playlistAvailable() {
      return this.api_available[this.playlistUri];
    },
    playlistUri() {
      return this.url.split("/").pop();
    },
    songIndex() {
      if (this.playlist_current.uri !== this.playlistUri) return -1;
      return this.playlist_current.index;
    },
    tracks() {
      return this.playlist.tracks;
    }
  },
  mounted() {
    scPlayer.resolve(this.url, playlist => {
      this.setPlaylist({ playlist, uri: this.playlistUri });
      if (this.preload && !this.player.playing)
        this.setCurrentSong({ index: 0, uri: this.playlistUri });
    });

    // Check if Loading Playlist Failed
    setTimeout(() => {
      if (this.playlist === undefined)
        this.setApiAvailability({ uri: this.playlistUri, status: false });
    }, this.checkDelay);
  },
  methods: {
    ...mapMutations([
      "setApiAvailability",
      "setCurrentSong",
      "setPlaying",
      "setPlaylist"
    ]),
    ...mapActions(["pause"]),
    playTrack(url, trackIndex, id) {
      if (this.trackIsPlaying(id)) return this.pause();

      scPlayer.resolve(url, () => {
        this.setCurrentSong({ index: trackIndex, uri: this.playlistUri });
        this.setPlaying(true);
        scPlayer.play({ playlistIndex: trackIndex });
      });
    },
    trackIsPlaying(trackId) {
      return this.player.playing && trackId === this.current_song.id;
    }
  }
};
</script>
