<template>
  <div ref="form" id="ContactForm" class="form form--contact">
    <div class="form__messages text-center">
      <b-spinner v-if="show.loading" label="Submitting form"></b-spinner>

      <div v-if="show.failure" class="form__message form__message--failure">
        {{ message.failure }}
      </div>
      <div v-if="show.success" class="form__message form__message--success">
        {{ message.success }}
      </div>
    </div>

    <ValidationObserver v-slot="{ invalid }">
      <b-form @submit.prevent="onSubmit" v-if="show.form">
        <!-- First Row -->
        <div class="row mb-lg-5">
          <div class="col-12 col-lg-6 pr-lg-4">
            <validation-provider
              rules="required"
              name="name"
              v-slot="{ errors }"
            >
              <b-form-group
                id="Name"
                label="Name:"
                label-for="NameInput"
                label-sr-only
                class="mb-3 mb-lg-0"
              >
                <b-form-input
                  id="NameInput"
                  v-model="fields.name"
                  placeholder="name"
                ></b-form-input>
              </b-form-group>
              <span class="form-error">{{ errors[0] }}</span>
            </validation-provider>
          </div>

          <div class="col-12 col-lg-6 pl-lg-4">
            <validation-provider
              rules="email|required"
              name="email"
              v-slot="{ errors }"
            >
              <b-form-group
                id="Email"
                label="Email:"
                label-for="EmailInput"
                label-sr-only
                class="mb-3 mb-lg-0"
              >
                <b-form-input
                  id="EmailInput"
                  v-model="fields.email"
                  type="email"
                  placeholder="champagnepapi@6ixgod.com"
                ></b-form-input>
              </b-form-group>
              <span class="form-error">{{ errors[0] }}</span>
            </validation-provider>
          </div>
        </div>

        <!-- Second Row -->
        <div class="row align-items-end no-gutters">
          <div class="col-12 col-lg-9 mb-4 mb-lg-0">
            <validation-provider
              rules="min:30|required"
              name="message"
              v-slot="{ errors }"
            >
              <b-form-group
                id="Message"
                label="Message:"
                label-for="MessageInput"
                label-sr-only
              >
                <b-form-textarea
                  id="MessageInput"
                  v-model="fields.message"
                  placeholder="what are you working on?"
                  rows="3"
                  max-rows="6"
                ></b-form-textarea>
              </b-form-group>
              <span class="form-error form-error--neutral">
                {{ errors[0] }}
              </span>
            </validation-provider>
          </div>

          <div class="col-12 col-lg-3">
            <div class="text-right pl-0 pl-lg-5">
              <b-button type="submit" class="btn--100" :disabled="invalid">
                send
              </b-button>
            </div>
          </div>
        </div>

        <!-- Honeypot -->
        <input
          v-model="honeypot"
          type="text"
          name="subject"
          class="sr-only"
          tabindex="-1"
          autocomplete="off"
          aria-hidden
        />
      </b-form>
    </ValidationObserver>
  </div>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
import { email, min, required } from "vee-validate/dist/rules";
import * as ajax from "@/utilities/ajax";
import config from "@/config";

// Validation Rules
extend("min", {
  ...min,
  message: "write me a little more unless you're a spam bot"
});
extend("email", email);
extend("required", required);

export default {
  name: "ContactForm",
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data() {
    return {
      fields: {
        email: "",
        message: "",
        name: ""
      },
      hideDelay: 5000,
      honeypot: "", // Used as a honeypot
      message: {
        failure: "There was an issue, please try again.",
        success: "Your message was received successfully."
      },
      show: {
        failure: false,
        form: true,
        loading: false,
        success: false
      }
    };
  },
  computed: {
    formData() {
      return Object.assign(
        {
          _email: {
            from: this.fields.name,
            replyto: this.fields.email,
            subject: `Contact: ${this.fields.name}`
          },
          _honeypot: this.subject
        },
        this.fields
      );
    }
  },
  methods: {
    onFormComplete(message) {
      this.show.loading = false;
      this.show[message] = true;

      setTimeout(() => this.resetForm(), this.hideDelay);
    },
    onSubmit() {
      // Set to Loading
      this.show.form = false;
      this.show.loading = true;

      // Scroll to Top of Form
      this.$smoothScroll({ scrollTo: this.$refs.form, offset: -100 });

      // Ajax Form Submission
      return ajax
        .post(config.forms.contact, this.formData)
        .then(() => this.onFormComplete("success"))
        .catch(() => this.onFormComplete("failure"));
    },
    resetForm() {
      this.fields = {
        email: "",
        message: "",
        name: ""
      };
      this.show = {
        failure: false,
        form: true,
        loading: false,
        success: false
      };
    }
  }
};
</script>
