<template>
  <div class="footer bg-color-black-light" v-once>
    <div class="container position-relative">
      <div class="footer__content">
        <div class="row align-items-end">
          <!-- Left -->
          <div class="col-12 col-sm-4">
            <ul class="footer__menu d-none d-md-block">
              <li
                v-for="item in menu"
                v-bind:key="item.value"
                class="footer__menu-item"
              >
                <a
                  class="footer__menu-link"
                  :class="{ 'footer__menu-link--bold': item.emphasize }"
                  :href="item.value"
                  v-smooth-scroll
                >
                  {{ item.name }}
                </a>
              </li>
            </ul>
          </div>

          <!-- Center -->
          <div class="col-12 col-sm-4 text-center">
            <div class="footer__title font-bold">
              Music <span class="font-regular">by</span> Darcy<span
                class="color-red"
                >.</span
              >
            </div>

            <ul
              class="footer__social list-unstyled d-flex justify-content-center align-items-center mb-1 mb-md-0"
            >
              <li v-for="(link, name) in social" v-bind:key="name">
                <a
                  :href="link"
                  target="_blank"
                  class="footer__social-link font-30"
                  @click.prevent="
                    onClick({
                      category: 'Social Profiles',
                      action: 'Link Click',
                      label: name,
                      url: link
                    })
                  "
                >
                  <span class="sr-only">
                    Link to {{ name }} social media profile
                  </span>
                  <i :class="`fab fa-${name}`"></i>
                </a>
              </li>
            </ul>
          </div>

          <!-- Right -->
          <div class="col-12 col-sm-4">
            <div
              class="footer__copyright text-center text-md-right mt-4 mt-md-0"
            >
              Copyright {{ new Date().getFullYear() }}
            </div>
          </div>
        </div>

        <div class="footer__stencil" aria-hidden>
          <div class="stencil stencil--white">Darcy.</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "@/config";
import { mapState } from "vuex";

export default {
  name: "Footer",
  data() {
    return {
      social: config.social
    };
  },
  computed: {
    ...mapState(["menu"])
  },
  methods: {
    onClick({ category, action, label, url }) {
      // DataLayer Event
      this.$gtm.trackEvent({
        event: "eventTracking",
        category,
        action,
        label,
        value: url
      });

      // Redirect
      window.location = url;
    }
  }
};
</script>
