<template>
  <div class="player" :class="classes">
    <!-- Controls -->
    <div class="player__controls">
      <button
        @click="prev()"
        class="player__control player__control--previous"
        aria-label="Previous Song"
        :disabled="disabled"
      >
        <i class="fas fa-backward"></i>
      </button>

      <button
        v-if="!player.playing"
        @click="play()"
        class="player__control player__control--play"
        aria-label="Play Song"
        :disabled="disabled"
      >
        <i class="fas fa-play"></i>
      </button>

      <button
        v-if="player.playing"
        @click="pause()"
        class="player__control player__control--pause"
        aria-label="Pause Song"
        :disabled="disabled"
      >
        <i class="fas fa-pause"></i>
      </button>

      <button
        @click="next()"
        class="player__control player__control--next"
        aria-label="Next Song"
        :disabled="disabled"
      >
        <i class="fas fa-forward"></i>
      </button>
    </div>

    <!-- Info -->
    <div class="player__song">
      <!-- Artwork -->
      <img
        v-if="artwork"
        :src="artwork"
        :alt="`Artwork for song ${current_song.title}`"
        class="player__artwork"
        :class="{ 'player__artwork--spin': player.playing }"
      />
      <div v-else class="player__artwork player__artwork--fallback"></div>

      <!-- Metadata -->
      <div class="player__metadata">
        <div class="player__title text-truncate">
          <span v-if="current_song.title">{{ current_song.title }}</span>
          <span v-else>Feed Me Songs</span>
        </div>

        <span class="player__time">
          {{ timeCountdown | timestamp }}
        </span>
      </div>

      <!-- Progress -->
      <b-progress
        :value="player.playhead"
        :max="current_song.duration"
        class="player__progress"
      />
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

export default {
  name: "Player",
  props: {
    classes: {
      type: String,
      default: ""
    }
  },
  computed: {
    ...mapState(["current_song", "player"]),
    ...mapGetters([
      "artwork",
      "firstSongIndex",
      "lastSongIndex",
      "nextSongIndex",
      "prevSongIndex",
      "streamUrl",
      "timeCurrent",
      "timeCountdown",
      "timeDuration"
    ]),
    disabled() {
      return this.current_song.id === null || this.player.processing;
    }
  },
  methods: {
    ...mapMutations(["setCurrentSong", "setPlaying", "setProcessing"]),
    ...mapActions(["next", "play", "pause", "prev", "stop"])
  }
};
</script>
